html {
    height: 100%;
}
body {
    min-height: 100%;
}
/* bootstrap will overide the custom css without the !important */
body, .info1 {
    background: #F2F3F5!important;
}
.info1, .info2 {
    min-height: 500px;
}
.navbar, .info2 {
    background: #003055;
}

p, h1, h2, h4, h5, .navbar-nav, .navbar-light, .navbar-brand, .nav-link, .view-btn {
    font-family: 'Roboto Mono', monospace;
    color: #F2F3F5;
}
/* bootstrap will overide the custom css without the !important */
.navbar-light, .navbar-brand, .nav-link {
    color: #F2F3F5!important;
}
h4 {
    color: #003055;
}

h1 {
    font-size: 50px;
}

.my-h2 {
    font-weight: 900;
    font-size: 123px;
    line-height: 120px;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 5px #ff4000;
    /* this overlap is intentional */
    position:relative;
    bottom: 30px;
    text-shadow: 8px 8px #21252917;
}

.my-h4,
.my-h5 {
    font-size: 20px;
}

.row {
    padding-bottom: 48px;
}
/* bootstrap will overide the custom css without the !important */
.row-margin {
    margin-left: auto!important;
    margin-right: auto!important;
}
.navbar-nav {
    line-height: 2.5;
}
/* bootstrap will overide the custom css without the !important */
.navbar-toggler {
    border-color: #ff4000!important;
}
.hero, .banner {
    min-height: 900px;
    background-image: linear-gradient(
      to bottom,
      #0030557e,
      rgba(0, 0, 0, 0.7)
    ),
    url(/static/media/hero.f0e14c91.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.banner {
    min-height: 300px;
}

.dot1, .dot2, .dot3, .dot4 {
    max-width: 25px;
    float: left;
}
.pop{
    z-index: 1040;
    position: fixed;
    top: 60px;
    left: 40%;
    max-height: 25px;
}
.social-icon {
    height: 40px;
    width:auto;
    margin:10px 20px 10px 0px;
}

.pageTitle {
    margin-top: 220px;
}

.profile-img {
    max-width: 200px;
    width: 100%;
    height: auto;
    margin-top: 100px;
    margin-bottom: 30px;
    margin-left: auto;
    margin-right: auto;
    border: 3px solid #ff4000;
}
.homeThumb {
    max-width: 100%;
    object-fit: cover;
    vertical-align: middle;
    margin: 10px;
    width: 162px;
    height: 162px;
}
.thumb {
    max-width: 100%;
    object-fit: cover;
    vertical-align: middle;
    margin: 10px;
    width: 210px;
    height: 210px;
}

@media (max-width: 767px) {
    .thumb {
        width: 172px;
        height: 172px;
    }
    .pageTitle {
        margin: 220px 20px 0px 20px;
    }
}
@media (max-width: 413px) {
    .thumb {
        width: 152px;
        height: 152px;
    }
}
.modalImg{
    margin-left: auto;
    margin-right:auto;
}
.hide {
    display:none!important;
}
/* bootstrap will overide the custom css without the !important */
.view-btn {
    display: inline-block;
    font-weight: 400;
    color: #212529!important;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    background-color: #ff4000;
    border: 1px solid #ff4000;
    margin: 10px;
}
.pushFooter {
    padding-bottom:10%;
}
footer {
    background-color: #003055;
    padding: 20px;
    border-top: 3px solid #ff4000;
    color:#F2F3F5;
    font-style: italic;
    font-size: 12px;
}

@media (max-width: 576px) {

    .my-h2 {
      font-size: 53px;
      line-height: 53px;
      font-weight: 500;
      -webkit-text-stroke: 3px #ff4000;
      bottom: 12px;
    }

    h4,
    h5 {
      margin: 0px 20px 50px 20px;
    }
    .my-h4,
    .my-h5 {
    font-size: 14px;
    font-weight: 400;
    }
    .dot1, .dot2, .dot3, .dot4 {
        display:none!important;
    }
}

